import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { IcoMoonIcon } from '../../interfaces/interfaces';

/**
 * Generated class for the IconDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-icon-detail',
  templateUrl: 'icon-detail.html',
})
export class IconDetailPage {

  public icon: IcoMoonIcon;
  public version: string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams
  ) {
    this.icon = navParams.data.icon;
    this.version = navParams.data.version;
  }

  ionViewDidLoad() { }

}



